import PropTypes from "prop-types";
import React, { Fragment, useEffect, useState } from "react";
import MetaTags from "react-meta-tags";
import { BreadcrumbsItem } from "react-breadcrumbs-dynamic";
import Card from "react-bootstrap/Card";
import Accordion from "react-bootstrap/Accordion";
import LayoutOne from "../../layouts/LayoutOne";
import Breadcrumb from "../../wrappers/breadcrumb/Breadcrumb";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { ToastProvider, useToasts } from "react-toast-notifications";
import AccountBalanceWalletIcon from "@mui/icons-material/AccountBalanceWallet";
import swal from "sweetalert";

const MyAccount = ({ location, user }) => {
  const [loading, setLoading] = useState(false);
  const [addaddress, setAddress] = useState({});
  const { pathname } = location;
  const { addToast } = useToasts();
  const history = useHistory();
  const [EditDeatails, setEditDeatails] = useState({});

  const {
    register,
    formState: { errors },
    handleSubmit,
    setValue,
    trigger,
  } = useForm({
    mode: "onBlur",
  });
  const {
    register: register2,
    formState: { errors: errors2 },
    handleSubmit: handleSubmit2,
  } = useForm({
    mode: "onBlur",
  });

  useEffect(async () => {
    if (!user) {
      history.push("/login-register");
    } else {
      const users = user.user;
      const phones = user.phone;

      try {
        const config = {
          headers: {
            "Content-type": "application/json",
            "auth-token": user.token,
          },
        };
        const { data } = await axios.post(
          "/api/user/user-deatails-get",
          { users, phones },
          config
        );
        setAddress(data?.Address);
        setEditDeatails(data);
        setValue("username", data.name);
        setValue("email", data.email);
        setValue("phoneNumber", data.phone);
      } catch (error) {
        addToast("Somthing Went Wrong Pleass Try After Some Time !", {
          appearance: "error",
          autoDismiss: true,
        });
      }
    }
  }, [loading]);
  //edit submit function
  const onSubmit = async (data) => {
    const email = data.email;
    const phone = data.phoneNumber;
    const name = data.username;
    const originalPhone = user.phone;
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
          "auth-token": user.token,
        },
      };

      const { data } = await axios.post(
        "/api/user/edit-user-deatails",
        {
          email,
          phone,
          name,
          originalPhone,
        },
        config
      );
      addToast("Succes", { appearance: "success", autoDismiss: true });
      history.push("/");
    } catch (error) {
      addToast(error.response.data, { appearance: "error", autoDismiss: true });
    }
  };

  //address taking function

  const onSubmitpassword = async (data) => {
    const Oldpassword = data.Oldpassword;
    const NewPassword = data.password;
    const users = user.user;
    const phones = user.phone;
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
          "auth-token": user.token,
        },
      };
      const { data } = await axios.put(
        "/api/user/edit-user-password",
        {
          Oldpassword,
          NewPassword,
          users,
          phones,
        },
        config
      );
      history.push("/");
      addToast("Successfully Changed", {
        appearance: "success",
        autoDismiss: true,
      });
    } catch (error) {
      addToast(error.response.data, { appearance: "error", autoDismiss: true });
    }
  };

  //delete address function
  const DelateAddress = async () => {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this data file!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then(async (willDelete) => {
      if (willDelete) {
        console.log(user);
        console.log(user.CUST_ID);
        const userId = user.CUST_ID;
        try {
          const config = {
            headers: {
              "Content-type": "application/json",
              "auth-token": user.token,
            },
          };
          const { data } = await axios.post(
            "/api/user/delete-user-addres",
            { userId },
            config
          );
          setLoading(true);
          setLoading(false);
          swal("Successfully Deleted!", {
            icon: "success",
          });
        } catch (error) {
          swal("Somthing Went Wrong!", {
            icon: "error",
          });
        }
      } else {
        swal("Your Data Is Safe");
      }
    });
  };

  return (
    <ToastProvider>
      <Fragment>
        <MetaTags>
          <title>Moffa | My Account</title>
          <meta
            name="description"
            content="Compare page of flone react minimalist eCommerce template."
          />
        </MetaTags>
        <BreadcrumbsItem to={process.env.PUBLIC_URL + "/"}>
          Home
        </BreadcrumbsItem>
        <BreadcrumbsItem to={process.env.PUBLIC_URL + pathname}>
          My Account
        </BreadcrumbsItem>
        <LayoutOne headerTop="visible">
          {/* breadcrumb */}
          <Breadcrumb />
          <div className="myaccount-area pb-80 pt-100">
            <div className="container">
              <div className="row">
                <div className="ml-auto mr-auto col-lg-9">
                  <div className="myaccount-wrapper">
                    <Accordion defaultActiveKey="0">
                      <Card className="single-my-account mb-20">
                        <Card.Header className="panel-heading">
                          <Accordion.Toggle variant="link" eventKey="0">
                            <h3 className="panel-title">
                              <span>1 .</span> Edit your account information{" "}
                            </h3>
                          </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                          <Card.Body>
                            <form key={1} onSubmit={handleSubmit(onSubmit)}>
                              <div className="myaccount-info-wrapper">
                                <div className="account-info-wrapper">
                                  <h4>My Account Information</h4>
                                  <h5>Your Personal Details</h5>
                                </div>
                                <div className="row">
                                  <div className="col-lg-6 col-md-6">
                                    <div className="billing-info">
                                      <label>First Name</label>
                                      <input
                                        type="text"
                                        {...register("username", {
                                          required: "username is required",
                                          pattern: {
                                            value: /^[a-zA-Z\s]*$/,
                                            message: "invalid username address",
                                          },
                                        })}
                                        onKeyUp={() => {
                                          trigger("username");
                                        }}
                                      />
                                      {errors.username && (
                                        <small className="text-danger">
                                          {errors.username.message}
                                        </small>
                                      )}
                                    </div>
                                  </div>
                                  {/* <div className="col-lg-6 col-md-6">
                                <div className="billing-info">
                                  <label>Last Name</label>
                                  <input type="text" />
                                </div>
                              </div> */}
                                  <div className="col-lg-12 col-md-12">
                                    <div className="billing-info">
                                      <label>Email Address</label>
                                      <input
                                        type="email"
                                        {...register("email", {
                                          required: "email is required",
                                          pattern: {
                                            value:
                                              /^[\w-]+@([\w-]+\.)+[\w-]{2,4}$/,
                                            message: "invalid email address",
                                          },
                                        })}
                                        onKeyUp={() => {
                                          trigger("email");
                                        }}
                                      />
                                      {errors.email && (
                                        <small className="text-danger">
                                          {errors.email.message}
                                        </small>
                                      )}
                                    </div>
                                  </div>
                                  <div className="col-lg-6 col-md-6">
                                    <div className="billing-info">
                                      <label>Telephone</label>
                                      <input
                                        type="text"
                                        {...register("phoneNumber", {
                                          required: "phoneNumber is required",
                                          pattern: {
                                            value:
                                              /^[(]?[0-9]{3}[)]?[-\s]?[0-9]{3}[-\s]?[0-9]{4,6}$/,
                                            message: "invalid phoneNumber",
                                          },
                                        })}
                                        onKeyUp={() => {
                                          trigger("phoneNumber");
                                        }}
                                      />
                                      {errors.phoneNumber && (
                                        <small className="text-danger">
                                          {errors.phoneNumber.message}
                                        </small>
                                      )}
                                    </div>
                                  </div>
                                  {/* <div className="col-lg-6 col-md-6">
                                <div className="billing-info">
                                  <label>Fax</label>
                                  <input type="text" />
                                </div>
                              </div> */}
                                </div>
                                <div className="billing-back-btn">
                                  <div className="billing-btn">
                                    <button type="submit">Continue</button>
                                  </div>
                                </div>
                              </div>
                            </form>
                          </Card.Body>
                        </Accordion.Collapse>
                      </Card>
                      <Card className="single-my-account mb-20">
                        <Card.Header className="panel-heading">
                          <Accordion.Toggle variant="link" eventKey="1">
                            <h3 className="panel-title">
                              <span>2 .</span> Change your password
                            </h3>
                          </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="1">
                          <Card.Body>
                            <form
                              key={2}
                              onSubmit={handleSubmit2(onSubmitpassword)}
                            >
                              <div className="myaccount-info-wrapper">
                                <div className="account-info-wrapper">
                                  <h4>Change Password</h4>
                                  <h5>Your Password</h5>
                                </div>
                                <div className="row">
                                  <div className="col-lg-12 col-md-12">
                                    <div className="billing-info">
                                      <label>Password</label>
                                      <input
                                        type="password"
                                        {...register2("Oldpassword", {
                                          required: "password is required",
                                          pattern: {
                                            value: /^[a-zA-Z]{8,22}$/,
                                            message: "invalid password",
                                          },
                                        })}
                                        onKeyUp={() => {
                                          trigger("Oldpassword");
                                        }}
                                      />

                                      {errors2.Oldpassword && (
                                        <small className="text-danger">
                                          {errors2.Oldpassword.message}
                                        </small>
                                      )}
                                    </div>
                                  </div>
                                  <div className="col-lg-12 col-md-12">
                                    <div className="billing-info">
                                      <label>Enter New Password</label>
                                      <input
                                        type="password"
                                        {...register2("password", {
                                          required: "password is required",
                                          pattern: {
                                            value: /^[a-zA-Z]{8,22}$/,
                                            message: "invalid password",
                                          },
                                        })}
                                        onKeyUp={() => {
                                          trigger("password");
                                        }}
                                      />

                                      {errors2.password && (
                                        <small className="text-danger">
                                          {errors2.password.message}
                                        </small>
                                      )}
                                    </div>
                                  </div>
                                </div>
                                <div className="billing-back-btn">
                                  <div className="billing-btn">
                                    <button type="submit">Continue</button>
                                  </div>
                                </div>
                              </div>
                            </form>
                          </Card.Body>
                        </Accordion.Collapse>
                      </Card>
                      {addaddress && (
                        <Card className="single-my-account mb-20">
                          <Card.Header className="panel-heading">
                            <Accordion.Toggle variant="link" eventKey="2">
                              <h3 className="panel-title">
                                <span>3 .</span> Modify your address book
                                entries{" "}
                              </h3>
                            </Accordion.Toggle>
                          </Card.Header>
                          <Accordion.Collapse eventKey="2">
                            <Card.Body>
                              <div className="myaccount-info-wrapper">
                                <div className="account-info-wrapper">
                                  <h4>Address Book Entries</h4>
                                </div>
                                <div className="entries-wrapper">
                                  <div className="row">
                                    <div className="col-lg-6 col-md-6 d-flex align-items-center justify-content-center">
                                      <div className="entries-info text-center">
                                        <p>
                                          {addaddress.Name},
                                          {addaddress.LastName}
                                        </p>
                                        <p>{addaddress.StreetAddress}</p>
                                        <p>
                                          {addaddress.TownCity},
                                          {addaddress.State},
                                          {addaddress.Pincode}
                                        </p>
                                        <p>{addaddress.Email}</p>
                                        <p>{addaddress.PhoneNumber}</p>
                                      </div>
                                    </div>
                                    <div className="col-lg-6 col-md-6 d-flex align-items-center justify-content-center">
                                      <div className="entries-edit-delete text-center">
                                        <button onClick={DelateAddress}>
                                          Delete
                                        </button>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                                <div className="billing-back-btn">
                                  <div className="billing-btn">
                                    {/* <button type="submit">Continue</button> */}
                                  </div>
                                </div>
                              </div>
                            </Card.Body>
                          </Accordion.Collapse>
                        </Card>
                      )}
                      {!user?.user && (
                        <Card className="single-my-account mb-20">
                          <Card.Header className="panel-heading">
                            <Accordion.Toggle variant="link" eventKey="3">
                              <h3 className="panel-title">
                                <span>4 .</span> Wallet Information
                              </h3>
                            </Accordion.Toggle>
                          </Card.Header>
                          <Accordion.Collapse eventKey="3">
                            <Card.Body>
                              <div className="myaccount-info-wrapper">
                                <div className="account-info-wrapper">
                                  <h4>Wallet Amount Details</h4>
                                </div>
                                <div className="entries-wrapper">
                                  <div className="row">
                                    <div className="col-lg-6 col-md-6 d-flex align-items-center justify-content-center">
                                      <div className="entries-info text-center">
                                        <p>
                                          <AccountBalanceWalletIcon
                                            style={{
                                              fontSize: "35px",
                                              color: "red",
                                            }}
                                          />
                                          :{" "}
                                          <span
                                            style={{
                                              fontSize: "20px",
                                              color: "green",
                                            }}
                                          >
                                            {" "}
                                            ₹{EditDeatails.wallet}{" "}
                                          </span>
                                        </p>
                                      </div>
                                    </div>
                                    {/* <div className="col-lg-6 col-md-6 d-flex align-items-center justify-content-center">
                                      <div className="entries-edit-delete text-center">
                                        <button className="edit">Edit</button>
                                        <button>Delete</button>
                                      </div>
                                    </div> */}
                                  </div>
                                </div>
                                <div className="billing-back-btn">
                                  <div className="billing-btn">
                                    <button type="submit">ADD AMOUNT</button>
                                  </div>
                                </div>
                              </div>
                            </Card.Body>
                          </Accordion.Collapse>
                        </Card>
                      )}
                    </Accordion>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </LayoutOne>
      </Fragment>
    </ToastProvider>
  );
};

MyAccount.propTypes = {
  location: PropTypes.object,
  user: PropTypes.object,
};
const mapStateToProps = (state) => {
  return {
    user: state.userData.user,
  };
};

export default connect(mapStateToProps)(MyAccount);
